import type { Who } from '~/utils/types';

import { useEffect } from 'react';
import Books from '~/components/books';
import Nav from '~/components/nav';
import { useNavigate, useParams } from '@remix-run/react';
import { getUserIdFromParams } from '~/utils/helpers';
import type { CoreLoaderData } from '~/loaders/core.server';

const localStorageKey = 'previous-who';
const desiredUserIds: Who[] = ['kate', 'chris'];

interface IProps {
	loaderData: CoreLoaderData;
}

export default function Core({ loaderData }: IProps) {
	const params = useParams();
	const navigate = useNavigate();

	const userId = getUserIdFromParams(params);

	useEffect(() => {
		if (userId && !desiredUserIds.includes(userId as Who)) {
			navigate('/');
			return;
		}

		const previousUserId = localStorage.getItem(localStorageKey);

		for (const desiredUserId of desiredUserIds) {
			if (previousUserId === desiredUserId && userId !== desiredUserId) {
				navigate(`/${previousUserId}`);
				return;
			}
		}
	}, []);

	useEffect(() => {
		if (userId === 'chris' || userId === 'kate') {
			localStorage.setItem(localStorageKey, userId);
		}
	}, [userId]);

	console.log(loaderData);

	return (
		<div className="p-8 w-full">
			<Nav userId={userId} />
			{userId && <Books />}
		</div>
	);
}
