import type { Who } from '~/utils/types';

import { useNavigate } from '@remix-run/react';
import { useEffect } from 'react';

interface IProps {
	userId: string | null;
}

const localStorageKey = 'previous-who';
const desiredUserIds: Who[] = ['kate', 'chris'];

export default function Nav({ userId }: IProps) {
	const navigate = useNavigate();

	useEffect(() => {
		if (userId && !desiredUserIds.includes(userId as Who)) {
			navigate('/');
			return;
		}

		const previousUserId = localStorage.getItem(localStorageKey);

		for (const desiredUserId of desiredUserIds) {
			if (previousUserId === desiredUserId && userId !== desiredUserId) {
				navigate(`/${previousUserId}`);
				return;
			}
		}
	}, []);

	useEffect(() => {
		if (userId === 'chris' || userId === 'kate') {
			localStorage.setItem(localStorageKey, userId);
		}
	}, [userId]);

	return (
		<header className="w-full border-b-2 border-b-emerald-600 pb-8 mb-8">
			<h1 className="text-4xl text-emerald-600 font-mono font-medium pb-4">
				Bookshelf
			</h1>
			<div className="text-xl">
				<label htmlFor="userId">Who are you?</label>
				<select
					id="userId"
					value={userId ?? 'none'}
					onChange={e => navigate(`/${e.target.value}`)}
					className="ml-2"
				>
					<option disabled value="none">
						Please choose
					</option>
					<option value="kate">Kate</option>
					<option value="chris">Chris</option>
				</select>
			</div>
		</header>
	);
}
